@import url(https://fonts.googleapis.com/css?family=Questrial&display=swap);
body {
  margin: 0;
  font-family: "Questrial", sans-serif, -apple-system,
    BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.app {
  text-align: center;
  background: #F0FFFF;
}

.app-content {
  margin-left: 10vw;
  margin-right: 10vw;
  min-height: 100vh;
}
.header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.title {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 1vmin);
  color: #333333;
}

.description {
  font-size: calc(12px + 1vmin);
  color: #222222;
}

.logo {
  height: 10vmin;
  padding: 5px;
}

.link {
  color: #d07c2c;
  display: inline-block;
}

.grid {
  margin: 50px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(14rem, 1fr));
  grid-gap: 2rem;
  cursor: pointer; }
  .grid .card {
    display: flex;
    flex-direction: column;
    -webkit-transition: background-color 0.5s ease;
    transition: background-color 0.5s ease;
    justify-content: space-between;
    align-items: center;
    background-color: #eeeeee; }
    .grid .card:hover {
      background-color: #dddddd; }
    .grid .card .info {
      padding: 1rem; }
    .grid .card img {
      object-fit: cover;
      width: 230px;
      height: 230px;
      margin-bottom: 1em; }
    .grid .card .desc {
      font-size: 18px;
      color: #222222; }

.details {
  margin: 20px;
  display: flex;
  flex-direction: column;
  font-size: 2em;
  justify-content: center;
  align-items: center; }

.frame {
  margin: 1em; }

